import React, { PropTypes, PureComponent } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import Filter from '../filter';
import CChart from '../chart';
import styles from './styles.scss';

class Main extends PureComponent {
  constructor(props) {
    super(props);

    this.minChartWidth = 315;
    this.minChartHeight = 150;
    this.maxChartHeight = 400;
  }

  componentDidMount() {
    setTimeout(() => window.dispatchEvent(new Event('resize')), 1000);
  }

  render() {
    const { charts, viewport, translate } = this.props;

    const viewWidth = viewport.screenWidth - 15;
    const width = viewWidth > this.minChartWidth ? viewWidth : this.minChartWidth;

    let height = width * 0.6 >> 0;
    height = height > this.minChartHeight ? height : this.minChartHeight;
    height = height < this.maxChartHeight ? height : this.maxChartHeight;

    let exWidth = (viewWidth - 10) / 2 >> 0;
    exWidth = exWidth > this.minChartWidth ? exWidth : this.minChartWidth;

    let exHeight = exWidth * 0.6 >> 0;
    exHeight = exHeight > this.minChartHeight ? exHeight : this.minChartHeight;
    exHeight = exHeight < this.maxChartHeight ? exHeight : this.maxChartHeight;

    return (
      <div id="root" className={styles.root}>
        <Filter />
        {charts.map(chart => {
          let classChange = undefined;
          let classGap = undefined;

          if (chart.change > 0) {
            classChange = styles.green;
          } else if (chart.change < 0) {
            classChange = styles.red;
          }

          if (chart.gap > 0) {
            classGap = styles.green;
          } else if (chart.gap < 0) {
            classGap = styles.red;
          }

          return (
            <div
              key={`${chart.base}-${chart.quote}-${chart.exchange}`}
              className={styles.chartWrapper}
            >
              <div className={styles.chart}>
                <CChart
                  base={chart.base}
                  quote={chart.quote}
                  exchange={chart.exchange}
                />
              </div>
              <div className={styles.details}>
                <table>
                  <tr>
                    <td>{translate.date}</td><td>{moment(chart.date).format('YYYY-MM-DD')}</td>
                  </tr>
                  <tr>
                    <td>{translate.open}</td><td>{chart.open.toFixed(4)}</td>
                  </tr>
                  <tr>
                    <td>{translate.high}</td><td>{chart.high.toFixed(4)}</td>
                  </tr>
                  <tr>
                    <td>{translate.close}</td><td>{chart.close.toFixed(4)}</td>
                  </tr>
                  <tr>
                    <td>{translate.low}</td><td>{chart.low.toFixed(4)}</td>
                  </tr>
                  <tr>
                    <td>{translate.volume}</td><td>{Math.round(chart.volume)}</td>
                  </tr>
                  <tr className={classChange}>
                    <td>{translate.change}</td><td>{chart.change.toFixed(4)}</td>
                  </tr>
                  <tr className={classChange}>
                    <td>{translate.changePercent}</td><td>{chart.changePercent.toFixed(4)}</td>
                  </tr>
                  <tr className={classGap}>
                    <td>{translate.gap}</td><td>{chart.gap.toFixed(4)}</td>
                  </tr>
                  <tr className={classGap}>
                    <td>{translate.gapPercent}</td><td>{chart.gapPercent.toFixed(4)}</td>
                  </tr>
                </table>
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

Main.propTypes = {
  charts: PropTypes.arrayOf(PropTypes.shape({
    base: PropTypes.string.isRequired,
    quote: PropTypes.string.isRequired,
    exchange: PropTypes.string.isRequired,
    date: PropTypes.number.isRequired,
    open: PropTypes.number.isRequired,
    high: PropTypes.number.isRequired,
    close: PropTypes.number.isRequired,
    low: PropTypes.number.isRequired,
    volume: PropTypes.number.isRequired,
    change: PropTypes.number.isRequired,
    changePercent: PropTypes.number.isRequired,
    gap: PropTypes.number.isRequired,
    gapPercent: PropTypes.number.isRequired,
  })).isRequired,
  viewport: PropTypes.shape({
    screenWidth: PropTypes.number,
  }).isRequired,
  translate: PropTypes.shape({
    date: PropTypes.string.isRequired,
    open: PropTypes.string.isRequired,
    high: PropTypes.string.isRequired,
    close: PropTypes.string.isRequired,
    low: PropTypes.string.isRequired,
    volume: PropTypes.string.isRequired,
    change: PropTypes.string.isRequired,
    changePercent: PropTypes.string.isRequired,
    gap: PropTypes.string.isRequired,
    gapPercent: PropTypes.string.isRequired,
  }).isRequired,
};

const mapStateToProps = state => ({
  charts: state.charts,
  viewport: state.viewport,
  translate: state.translate.charts,
});

export default connect(mapStateToProps)(Main);



