import React, { PropTypes, PureComponent } from 'react';
import { TYPES, TIMEFRAMES } from 'src/components/chart';
import Buttons from './buttons';
import styles from './styles.scss';

class Bar extends PureComponent {
  constructor(props) {
    super(props);

    this.onChangeType = this.onChangeType.bind(this);
    this.onChangeTimeframe = this.onChangeTimeframe.bind(this);
  }

  onChangeType(index) {
    if (TYPES[index].disabled) return;

    this.props.onChangeType(index);
  }

  onChangeTimeframe(index) {
    if (TIMEFRAMES[index].disabled) return;

    this.props.onChangeTimeframe(index);
  }

  render() {
    const { symbol, exchange, type, timeframe } = this.props;

    const types = Object.keys(TYPES).reduce((prev, curr) => ({
      ...prev,
      [curr]: (
        <div className={styles.icon} title={TYPES[curr].title}>
          <svg className={styles.icon}>
            <use xlinkHref={`#${TYPES[curr].icon}`} />
          </svg>
        </div>
      ),
    }), {});

    const timeframes = Object.keys(TIMEFRAMES).reduce((prev, curr) => ({
      ...prev,
      [curr]: (
        <div
          className={TIMEFRAMES[curr].disabled ? styles.iconDisabled : styles.icon}
        >{TIMEFRAMES[curr].title}</div>
      ),
    }), {});

    return (
      <div className={styles.bar}>
        <div className={styles.title}>
          <div className={styles.symbol}>{symbol}</div>
          {exchange && exchange.length > 0 && (
            <div className={styles.exchange}>{exchange}</div>
          )}
        </div>
        <Buttons
          className={styles.buttons}
          values={types}
          selected={type}
          onChange={this.onChangeType}
        />
        <div className={styles.separator} />
        <Buttons
          className={styles.buttons}
          values={timeframes}
          selected={timeframe}
          onChange={this.onChangeTimeframe}
        />
      </div>
    );
  }
}

Bar.propTypes = {
  symbol: PropTypes.string,
  exchange: PropTypes.string,
  type: PropTypes.string.isRequired,
  timeframe: PropTypes.string.isRequired,
  onChangeType: PropTypes.func.isRequired,
  onChangeTimeframe: PropTypes.func.isRequired,
};

Bar.defaultProps = {
  onChangeType: () => {},
  onChangeTimeframe: () => {},
};

export default Bar;



