import React, { PropTypes, PureComponent } from 'react';
import { connect } from 'react-redux';
import { filterSet, filterRequest } from 'src/actions';
import Langs from 'src/components/langs';
import styles from './styles.scss';

export const FILTERS = {
  PROFIT: 'profit',
  TRADES: 'trades',
};

class Filter extends PureComponent {
  componentWillMount() {
    const { active, onToggle } = this.props;
    onToggle(active);
  }

  render() {
    const { active, translate, onToggle } = this.props;

    return (
      <div className={styles.header}>
        <a
          href="https://cryptostock.pw"
          className={styles.logo}
        >CRYPTO<span className={styles.box}>STOCK</span></a>
        <Langs className={styles.langs} />
        <ul className={styles.menu}>
          {Object.keys(FILTERS).map(key => (
            <li
              key={FILTERS[key]}
              className={active === FILTERS[key] && styles.active}
              onClick={() => onToggle(FILTERS[key])}
            ><span>{translate[FILTERS[key]]}</span></li>
          ))}
        </ul>
      </div>
    );
  }
};

Filter.propTypes = {
  active: PropTypes.string.isRequired,
  translate: PropTypes.object.isRequired,
  onToggle: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  active: state.filter,
  translate: state.translate.filters,
});

const mapDispatchToProps = dispatch => ({
  onToggle: filter => {
    dispatch(filterSet(filter));
    dispatch(filterRequest(filter));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(Filter);



