import { FILTER_SET } from 'src/actions';
import { FILTERS } from 'src/components/filter';

export default (state = FILTERS.PROFIT, action) => {
  switch (action.type) {
    case FILTER_SET:
      return action.filter;

    default:
      return state;
  }
};



