 $color-accent;\n$progress-secondary-color: $color-accent-dark;\n$progress-disabled-color: $color-border;\n$circle-wrapper-width: 60;\n$circle-radius: 25;\n$scale-ratio: $circle-radius / 20;\n\n// scss-lint:disable ColorVariable\n$colors: (\n  $color-accent,\n  #de3e35,\n  #f7c223,\n  #1b9a59,\n  #b746af,\n  #98aa2d\n);\n// scss-lint:enable ColorVariable\n\n.linear {\n  display: inline-block;\n  position: relative;\n  background: $color-divider;\n  width: 100%;\n  height: $progress-height;\n  overflow: hidden;\n\n  &.indeterminate .value {\n    transform-origin: center center;\n    animation: linear-indeterminate-bar 1s linear infinite;\n  }\n}\n\n.value,\n.buffer {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  transform: scaleX(0);\n  transform-origin: left center;\n\n}\n\n.value {\n  background-color: $progress-main-color;\n}\n\n.buffer {\n  background-image: linear-gradient(to right, $progress-secondary-color, $progress-secondary-color),\n    linear-gradient(to right, $progress-main-color, $progress-main-color);\n}\n\n.circular {\n  display: inline-block;\n  position: relative;\n  transform: rotate(-90deg);\n  width: $circle-wrapper-width * 1px;\n  height: $circle-wrapper-width * 1px;\n\n  &.indeterminate {\n    .circle {\n      animation: circular-indeterminate-bar-rotate 2s linear infinite;\n    }\n\n    .path {\n      animation: circular-indeterminate-bar-dash 1.5s ease-in-out infinite;\n      stroke-dasharray: $scale-ratio * 1, $scale-ratio * 200;\n      stroke-dashoffset: 0;\n    }\n\n    &.multicolor .path {\n      animation: circular-indeterminate-bar-dash 1.5s ease-in-out infinite,\n        colors calc(1.5s * 4) ease-in-out infinite;\n    }\n  }\n}\n\n.circular[disabled],\n.linear[disabled] {\n  .value {\n    background-color: $progress-disabled-color;\n  }\n\n  .buffer {\n    background-image: linear-gradient(to right, $progress-secondary-color, $progress-secondary-color),\n      linear-gradient(to right, $progress-disabled-color, $progress-disabled-color);\n  }\n}\n\n.circle {\n  width: 100%;\n  height: 100%;\n}\n\n.path,\n.pathBuffer {\n  @include transition(stroke-dasharray);\n\n  fill: none;\n  stroke: $progress-main-color;\n  stroke-dasharray: 0, $scale-ratio * 200;\n  stroke-dashoffset: 0;\n  stroke-linecap: round;\n  stroke-miterlimit: 20;\n  stroke-width: 4;\n}\n\n.pathBuffer {\n  opacity: .2;\n  stroke-dashoffset: 10px;\n}\n\n@keyframes linear-indeterminate-bar {\n  0% {\n    transform: translate(-50%) scaleX(0);\n  }\n\n  50% {\n    transform: translate(-0%) scaleX(.3);\n  }\n\n  100% {\n    transform: translate(50%) scaleX(0);\n  }\n}\n\n@keyframes circular-indeterminate-bar-rotate {\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes circular-indeterminate-bar-dash {\n  0% {\n    stroke-dasharray: $scale-ratio * 1, $scale-ratio * 200;\n    stroke-dashoffset: $scale-ratio * 0;\n  }\n\n  50% {\n    stroke-dasharray: $scale-ratio * 89, $scale-ratio * 200;\n    stroke-dashoffset: $scale-ratio * -35;\n  }\n\n  100% {\n    stroke-dasharray: $scale-ratio * 89, $scale-ratio * 200;\n    stroke-dashoffset: $scale-ratio * -124;\n  }\n}\n\n@keyframes colors {\n  0% {\n    stroke: nth($colors, 1);\n  }\n\n  25% {\n    stroke: nth($colors, 2);\n  }\n\n  50% {\n    stroke: nth($colors, 3);\n  }\n\n  75% {\n    stroke: nth($colors, 4);\n  }\n\n  100% {\n    stroke: nth($colors, 1);\n  }\n}\n","// Baby Blue - Dusty Yellow\n$color-primary: #222;\n//$color-primary-light: #494949;\n$color-primary-light: #333;\n$color-primary-dark: #000;\n$color-accent: #0078c6;\n$color-accent-light: #5aa6f9;\n$color-accent-dark: #004d95;\n","// brand colors\n@import '../themes/black-blue';\n\n$color-text: #3c3950;\n$color-text-second: #748793;\n$color-text-accent: #fff;\n$color-text-fainted: rgba(0, 0, 0, .5);\n\n$color-error: #dd2c00;\n\n$color-white: #fff;\n$color-silver: #c0c0c0;\n$color-marengo: #4c5866;\n$color-graphite: #474a51;\n$color-anthracite: #464451;\n$color-blue-black: #151719;\n$colo