import React, { PropTypes, PureComponent } from 'react';
import moment from 'moment';
import { format, timeFormat } from 'd3';
import { ChartCanvas, Chart } from 'react-stockcharts';
import {
  BarSeries,
  CandlestickSeries,
  LineSeries,
} from 'react-stockcharts/lib/series';
import TradeBarSeries from './types/TradeBarSeries';
import {
  CrossHairCursor,
  MouseCoordinateX,
  MouseCoordinateY,
  CurrentCoordinate,
  EdgeIndicator,
} from 'react-stockcharts/lib/coordinates';
import { ema } from 'react-stockcharts/lib/indicator';
import { discontinuousTimeScaleProvider } from 'react-stockcharts/lib/scale';
import { OHLCTooltip, MovingAverageTooltip } from 'react-stockcharts/lib/tooltip';
import { XAxis, YAxis } from 'react-stockcharts/lib/axes';
import { fitWidth } from 'react-stockcharts/lib/helper';
import { last } from 'react-stockcharts/lib/utils';
import Bar from './bar';
import Aligner from 'src/utils/aligner';
import { Progress } from 'src/components/fields';
import { chartGetData } from 'src/actions';
import styles from './styles.scss';
import { connect } from 'react-redux';


export const TYPE_CODES = {
  LINE: 'line',
  CANDLESTICK: 'candlestick',
  BAR: 'bar',
};

export const TYPES = {
  [TYPE_CODES.LINE]: {
    icon: 'icon-chart-line',
    title: 'Line',
  },
  [TYPE_CODES.CANDLESTICK]: {
    icon: 'icon-chart-candlestick',
    title: 'Candlestick',
  },
  [TYPE_CODES.BAR]: {
    icon: 'icon-chart-bar',
    title: 'Bar',
  },
};

export const TIMEFRAME_CODES = {
  M5: 'm5',
  H1: 'h1',
  D1: 'd1',
  W1: 'w1',
  MN1: 'mn1',
};

export const TIMEFRAMES = {
  [TIMEFRAME_CODES.M5]: {
    title: '5M',
    disabled: true,
  },
  [TIMEFRAME_CODES.H1]: {
    title: '1H',
    disabled: true,
  },
  [TIMEFRAME_CODES.D1]: {
    title: '1D',
  },
  [TIMEFRAME_CODES.W1]: {
    title: '1W',
    disabled: true,
  },
  [TIMEFRAME_CODES.MN1]: {
    title: 'MN',
    disabled: true,
  },
};

const prepareBar = item => ({
  ...item,
  date: moment(item.date).toDate(),
});

class CChart extends PureComponent {
  constructor(props, context) {
    super(props, context);

    const { type, timeframe } = props;

    this.margin = { left: 40, right: 50, top: 20, bottom: 100 };

    this.state = {
      type,
      timeframe,
      overlay: true,
      bars: [],
      rest: [],
    };

    this.canRequest = true;

    this.init = this.init.bind(this);
    this.requestData = this.requestData.bind(this);
    this.onChangeType = this.onChangeType.bind(this);
    this.onChangeTimeframe = this.onChangeTimeframe.bind(this);
    this.onHideOverlay = this.onHideOverlay.bind(this);
  }

  requestData() {
    const { timeframe } = this.state;
    const { base, quote, exchange, match, chart } = this.props;

    chartGetData(timeframe, base, quote, exchange, match).then(response => {
      const bars = response.bars.map(prepareBar);
      const rest = response.rest.map(prepareBar);

      // don't include the bars later than the statistic data
      for (let i = bars.length - 1; i > 0; i--) {
        if (moment(bars[i].date).isAfter(moment(chart.date))) {
          bars.splice(i, 1);
        } else {
          break;
        }
      }

      this.setState({ bars, rest });

      if (this.canRequest) {
        //setTimeout(this.requestData, 2000);
      }
    }).catch(() => {
      if (this.canRequest) {
        setTimeout(this.requestData, 2000);
      }
    });
  }

  componentDidMount() {
    this.init();
  }

  componentWillUnmount() {
    this.canRequest = false;
  }

  componentWillReceiveProps(nextProps) {
    const { type, timeframe, base, quote, exchange } = nextProps;

    if (type !== this.props.type) {
      this.onChangeType(type);
    }

    if (
      this.props.base !== base ||
      this.props.quote !== quote ||
      this.props.exchange !== exchange
    ) {
      this.setState({
        type,
        timeframe,
        overlay: true,
        bars: [],
        rest: [],
      });

      this.init();
    }
  }

  init() {
    if (this.canRequest) {
      this.requestData();
    }
  }

  onChangeType(type) {
    this.setState({ type });
  }

  onChangeTimeframe(timeframe) {
    this.setState({ timeframe });
  }

  onHideOverlay() {
    this.setState({ overlay: false });
  }

  render() {
    const { bars, rest } = this.state;
    const { base, quote, exchange, width, height } = this.props;

    const symbol = `${base}/${quote}`;
    const _data = bars.concat(rest);

    const gridWidth = width - this.margin.left - this.margin.right;
    const gridHeight = height - this.margin.top - this.margin.bottom;

    const showGrid = true;
    const yGrid = showGrid ? { innerTickSize: -1 * gridWidth, tickStrokeOpacity: 0.2 } : {};
    const xGrid = showGrid ? { innerTickSize: -1 * gridHeight, tickStrokeOpacity: 0.2 } : {};

    let chartType = null;
    switch (this.state.type) {
      case TYPE_CODES.LINE:
        chartType = <LineSeries yAccessor={d => d.close} />;
        break;
      case TYPE_CODES.BAR:
        chartType = <TradeBarSeries />;
        break;
      case TYPE_CODES.CANDLESTICK:
      default:
        chartType = <CandlestickSeries />;
        break;
    }

    const ema20 = ema()
      .options({
        windowSize: 20,
        sourcePath: 'close',
      })
      .skipUndefined(true) // defaults to true
      .merge((d, c) => { d.ema20 = c; }) // eslint-disable-line no-param-reassign
      .accessor(d => d.ema20)
      .stroke('blue');

    const ema50 = ema()
      .options({
        windowSize: 50,
        sourcePath: 'close',
      })
      .skipUndefined(true) // defaults to true
      .merge((d, c) => { d.ema50 = c; }) // eslint-disable-line no-param-reassign
      .accessor(d => d.ema50)
      .stroke('red');

    const calculatedData = ema20(ema50(_data));
    const xScaleProvider = discontinuousTimeScaleProvider
      .inputDateAccessor(d => d.date);
    const {
      data,
      xScale,
      xAccessor,
      displayXAccessor,
    } = xScaleProvider(calculatedData);

    // number of bars, which should be fitted within canvas
    const barsCount = ((width - this.margin.left - this.margin.right) / 5) >> 0;

    const start = xAccessor(last(data));
    const end = xAccessor(data[Math.max(0, data.length - barsCount)]);
    const xExtents = [start, end];

    return (
      <div
        className={styles.chart}
      >
        <Bar
          symbol={symbol}
          exchange={exchange}
          type={this.state.type}
          timeframe={this.state.timeframe}
          onChangeType={this.onChangeType}
          onChangeTimeframe={this.onChangeTimeframe}
        />
        {_data.length > 0 && this.state.overlay && (
          <Aligner
            className={styles.overlay}
            onClick={this.onHideOverlay}
          >
            <div className={styles.overlayBlock}>
              <div className={styles.overlayText}>
                Click into the chart area<br />to switch on chart handling
              </div>
            </div>
          </Aligner>
        )}
        {_data.length > 0
          ? (
            <ChartCanvas
              ratio={1}
              width={width}
              height={height}
              margin={this.margin}
              padding={{
                left: 0,
                right: 20,
              }}
              type="hybrid"
              seriesName={symbol}
              data={data}
              xScale={xScale}
              xAccessor={xAccessor}
              displayXAccessor={displayXAccessor}
              xExtents={xExtents}
            >
              <Chart
                id={1}
                yExtents={[
                  d => {
                    const delta = 0.5 * (d.high - d.low) / 2;
                    return [d.high + delta, d.low - delta];
                  },
                  [ema20.accessor(), ema50.accessor()],
                ]}
                yMousePointerDisplayLocation="right"
                yMousePointerDisplayFormat={format('.2f')}
              >
                <XAxis axisAt="bottom" orient="bottom" {...xGrid} />
                <YAxis axisAt="right" orient="right" ticks={5} {...yGrid} />

                <MouseCoordinateX
                  at="bottom"
                  orient="bottom"
                  displayFormat={timeFormat('%H:%M')}
                />
                <MouseCoordinateY
                  at="right"
                  orient="right"
                  displayFormat={format('.2f')}
                />

                {chartType}

                <EdgeIndicator
                  dx={0}
                  itemType="last"
                  orient="right"
                  edgeAt="right"
                  yAccessor={d => d.close}
                  fill={d => `${d.close > d.open ? '#6BA583' : '#FF0000'}`}
                  lineStrokeDasharray="ShortDot"
                />

                <LineSeries yAccessor={ema20.accessor()} stroke={ema20.stroke()} />
                <LineSeries yAccessor={ema50.accessor()} stroke={ema50.stroke()} />
                <CurrentCoordinate yAccessor={ema20.accessor()} fill={ema20.stroke()} />
                <CurrentCoordinate yAccessor={ema50.accessor()} fill={ema50.stroke()} />

                <OHLCTooltip origin={[-40, 0]} />
                <MovingAverageTooltip
                  origin={[-38, 15]}
                  options={[
                    {
                      yAccessor: ema20.accessor(),
                      type: 'EMA',
                      stroke: ema20.stroke(),
                      windowSize: ema20.options().windowSize,
                      echo: 'some echo here',
                    },
                    {
                      yAccessor: ema50.accessor(),
                      type: 'EMA',
                      stroke: ema50.stroke(),
                      windowSize: ema50.options().windowSize,
                      echo: 'some echo here',
                    },
                  ]}
                  calculators={[ema20, ema50]}
                />
              </Chart>
              <Chart
                id={2}
                origin={(w, h) => [0, h + 30]}
                height={50}
                yExtents={d => d.quoteVolume}
                yMousePointerDisplayLocation="left"
                yMousePointerDisplayFormat={format('.0s')}
              >
                <YAxis axisAt="left" orient="left" ticks={5} tickFormat={format('.1s')} />
                <BarSeries yAccessor={d => d.quoteVolume} fill="#DDDDDD" />
              </Chart>
              <Chart
                id={3}
                origin={(w, h) => [0, h + 30]}
                height={50}
                yExtents={d => d.volume}
                yMousePointerDisplayLocation="right"
                yMousePointerDisplayFormat={format('.0s')}
              >
                <YAxis axisAt="right" orient="right" ticks={5} tickFormat={format('.1s')} />
                <BarSeries
                  yAccessor={d => d.volume}
                  fill={d => `${d.close > d.open ? '#6BA583' : '#FF0000'}`}
                />
              </Chart>
              <CrossHairCursor />
            </ChartCanvas>
          ) : (
            <div
              className={styles.loader}
              style={{ height }}
            ><Progress type="circular" /></div>
          )}
      </div>
    );
  }
}

CChart.propTypes = {
  base: PropTypes.string.isRequired,
  quote: PropTypes.string.isRequired,
  exchange: PropTypes.string.isRequired,
  match: PropTypes.arrayOf(PropTypes.string),
  width: PropTypes.number.isRequired,
  height: PropTypes.number.isRequired,
  type: PropTypes.oneOf(TYPE_CODES).isRequired,
  timeframe: PropTypes.oneOf(TIMEFRAME_CODES).isRequired,
  bars: PropTypes.array,
  rest: PropTypes.array,
  chart: PropTypes.shape({
    date: PropTypes.number.isRequired,
  }).isRequired,
};

CChart.defaultProps = {
  exchange: '',
  width: 500,
  height: 300,
  type: TYPE_CODES.CANDLESTICK,
  timeframe: TIMEFRAME_CODES.D1,
};

CChart = fitWidth(CChart);

const mapStateToProps = (state, props) => ({
  viewport: state.viewport,
  chart: state.charts.filter(
    chart => chart.base === props.base &&
             chart.quote === props.quote &&
             chart.exchange === props.exchange
  )[0],
});

export default connect(mapStateToProps)(CChart);



