import { request, responseJSON, chartSetData } from './';
import { FILTERS } from 'src/components/filter';

export const FILTER_SET = 'FILTER_SET';

export const filterSet = filter => ({
  type: FILTER_SET,
  filter,
});

export const filterRequest = filter => dispatch => {
  let params = '';

  switch (filter) {
    case FILTERS.TRADES:
      params = '?sort=volume&order=desc';
      break;
    case FILTERS.PROFIT:
    default:
      params = '?sort=changePercent&order=desc';
      break;
  }

  return request(`symbol/review${params}`, {}, 'POST')
    .then(responseJSON)
    .then(data => dispatch(chartSetData(data)));
};



