import { request, responseJSON } from 'src/actions';

export const CHART_SET_DATA = 'CHART_SET_DATA';

export const chartSetData = payload => ({
  type: CHART_SET_DATA,
  payload,
});

export const chartGetData = (timeframe, base, quote, exchange, match) => request(
  `symbol/${timeframe}/${base}/${quote}${
    exchange ? `/${exchange}` : ''
    }${
    !exchange && match ? `?match=${match.join(',')}` : ''
    }`
).then(responseJSON);



