import React, { PropTypes, PureComponent } from 'react';
import styles from './tabs.scss';

export class Tabs extends PureComponent {
  constructor(props, context) {
    super(props, context);

    this.state = {
      left: 0,
      width: 0,
    };

    this.updatePointer = this.updatePointer.bind(this);
    this.onResize = this.onResize.bind(this);
  }

  componentDidMount() {
    this.updatePointer(this.props, 100);
    window.addEventListener('resize', this.onResize);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.currentTab !== nextProps.currentTab) {
      this.updatePointer(nextProps);
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onResize);
  }

  onResize() {
    this.updatePointer(this.props);
  }

  updatePointer(props, delay) {
    // need the delay as browser width might be changed because of the context
    // for example, a scroll bar might be shown or the window has been resized
    setTimeout(() => {
      this.setState(this.getPointerOffset(props));
    }, delay || 10);
  }

  getPointerOffset(props) {
    const { currentTab } = props;
    const currentItem = this.refs[`item-${currentTab}`];
    const left = currentItem ? currentItem.offsetLeft : 0;
    const width = currentItem ? currentItem.clientWidth : 0;

    return {
      left,
      width,
    };
  }

  render() {
    const {
      tabs,
      currentTab,
      className,
      itemClassName,
      itemActiveClassName,
      pointerClassName,
      onChange,
      ...others  // eslint-disable-line comma-dangle
    } = this.props;
    const { left, width } = this.state;

    return (
      <div className={`${styles.root} ${className}`} {...others}>
        {tabs instanceof Array
          ? tabs.map(tab => (
            <div
              ref={`item-${tab.id}`}
              key={tab.id}
              className={`${
                tab.id === currentTab ? `${styles.itemActive} ${itemActiveClassName}` : styles.item
              } ${
                tab.disabled ? styles.itemDisabled : ''
              } ${
                itemClassName
              }`}
              onClick={() => !tab.disabled && onChange(tab.id)}
            >{tab.label}</div>
          ))
          : Object.keys(tabs).map(code => (
            <div
              ref={`item-${code}`}
              key={code}
              className={`${
                code === currentTab ? `${styles.itemActive} ${itemActiveClassName}` : styles.item
              } ${
                tabs[code].disabled ? styles.itemDisabled : ''
                } ${
                itemClassName
              }`}
              onClick={() => !tabs[code].disabled && onChange(code)}
            >{tabs[code]}</div>
          ))
        }
        <div
          className={`${styles.pointer} ${pointerClassName}`}
          style={{ left, width }}
        />
      </div>
    );
  }
}

Tabs.propTypes = {
  tabs: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.shape({
      id: PropTypes.any.isRequired,
      label: PropTypes.string.isRequired,
    })),
    PropTypes.object,
  ]).isRequired,
  currentTab: PropTypes.any.isRequired,
  className: PropTypes.string.isRequired,
  itemClassName: PropTypes.string.isRequired,
  itemActiveClassName: PropTypes.string.isRequired,
  pointerClassName: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
};

Tabs.defaultProps = {
  className: '',
  itemClassName: '',
  itemActiveClassName: '',
  pointerClassName: '',
};



