import React, { PropTypes } from 'react';
import styles from './aligner.scss';

const Aligner = ({ tag, children, ...others }) => {
  let _children = null;

  if (children.length > 1) {
    _children = children.map((child, index) => React.isValidElement(child)
      && React.cloneElement(
        child,
        {
          key: child.props && child.props.key || index,
          className: `${styles.aligner} ${child.props && child.props.className || ''}`,
        }
      )
      || <div key={index} className={styles.aligner}>{child}</div>
    );
  } else if (React.isValidElement(children)) {
    _children = React.cloneElement(
      children,
      {
        className: `${styles.aligner} ${children.props && children.props.className || ''}`,
      }
    );
  } else {
    _children = <div className={styles.aligner}>{children}</div>;
  }

  return React.createElement(
    tag,
    { ...others },
    <div className={styles.alignerEmpty} />,
    _children
  );
};

Aligner.propTypes = {
  tag: PropTypes.string.isRequired,
  className: PropTypes.string.isRequired,
  children: PropTypes.any.isRequired,
};

Aligner.defaultProps = {
  tag: 'div',
  className: '',
  children: [],
};

export default Aligner;



